var windowsToChange = [];
var windSreenIsNotCracked = "";
var chipBeenAlreadyRepaired = "";
var numOfChips = 0;
var chipOneArea = "";
var chipOneSize = "";
var chipTwoArea = "";
var chipTwoSize = "";
var chipThreeArea = "";
var chipThreeSize = "";

$(document).ready(function () {

    getBookflowDamagesVal();

    //WINDOW PICKER
    $('.car-windows').on('click', '.clickable.window', function () {
        var window = $(this);
        var windowType = window.data('original-title');
        var section = $(this).closest('.section-damaged-areas');
        var noRepairMsg = $('.notification-messages #noRepairMsg').html();

        if ($('#form-damage-details').hasClass('quote-damage')) {
            $('.next-section .price-quote:not(.d-none)').addClass('d-none');
            $('.next-section .call-us-section:not(.d-none)').addClass('d-none');
            $('.next-section .quote-btn.d-none').removeClass('d-none');
        }

        if (window.hasClass('active')) {
            window.removeClass('active');
            window.addClass('no-hover');
            windowsToChange.splice(windowsToChange.indexOf(window.attr('id')), 1);
            updateQuoteTable();

            if (windowType == 'Windscreen') {

                hideNextSection(section);

                if (windowsToChange.length > 0) {

                    $('.disabled-next-section').addClass('d-none');
                    if ($('#form-damage-details').hasClass('quote-damage')) {
                        $('.next-section').removeClass('d-none');
                        $('.next-section .quote-btn').removeClass('d-none');
                    } else {
                        $('.quote-holder').removeClass('d-none');
                    }
                }
            }
            else {
                if (section.find('.notification-box')[0]) {
                    section.find('.notification-box')[0].remove();
                }
            }

            if (windowsToChange.length == 0) {
                hideEndjourneyshowquote();
            }
        }
        else {
            window.addClass('active');
            if (windowType == 'Windscreen') {
                showNextSection('.section-damage-details-chip-size');
            }
            else {
                //Reset next global values
                windSreenIsNotCracked = "";
                chipBeenAlreadyRepaired = "";
                numOfChips = 0;
                chipOneArea = "";
                chipOneSize = "";
                chipTwoArea = "";
                chipTwoSize = "";
                chipThreeArea = "";
                chipThreeSize = "";
                endJourneyShowQuote(section, noRepairMsg);
            }
            windowsToChange.push(window.attr('id'));

            updateQuoteTable();
        }
        //console.log('windowsToChange =' + windowsToChange);

    });
    $('.car-windows').on('mousemove', function () {
        $(this).find('.clickable.window.no-hover').removeClass('no-hover');
    });

    // section-damage-details-chip-size
    $('.section-damage-details-chip-size').on('change', '.new-big-circles-radio input', function () {
        var section = $(this).closest('.section-damage-details-chip-size');
        windSreenIsNotCracked = $(this).val();
        var noRepairMsg = $('.notification-messages #noRepairMsg').html();

        if (windSreenIsNotCracked == 'True') {
            showNextSection('.section-damage-details-chip-new-old');
            if (section.find('.notification-box')[0]) {
                section.find('.notification-box')[0].remove();
            }
        }
        else {
            hideNextSection(section);
            //Reset next global values
            chipBeenAlreadyRepaired = "";
            numOfChips = 0;
            chipOneArea = "";
            chipOneSize = "";
            chipTwoArea = "";
            chipTwoSize = "";
            chipThreeArea = "";
            chipThreeSize = "";
            endJourneyShowQuote(section, noRepairMsg);
        }

        //console.log('windSreenIsNotCracked: ' + windSreenIsNotCracked);
    });

    // section-damage-details-chip-new-old
    $('.section-damage-details-chip-new-old').on('change', '.small-squares-radio input', function () {
        var section = $(this).closest('.section-damage-details-chip-new-old');
        chipBeenAlreadyRepaired = $(this).val();
        var noRepairMsg = $('.notification-messages #noRepairMsg').html();


        if (chipBeenAlreadyRepaired == 'False') {
            showNextSection('.section-damage-details-chip-number');
            if (section.find('.notification-box')[0]) {
                section.find('.notification-box')[0].remove();
            }
        }
        else {
            hideNextSection(section);
            //Reset next global values
            numOfChips = 0;
            chipOneArea = "";
            chipOneSize = "";
            chipTwoArea = "";
            chipTwoSize = "";
            chipThreeArea = "";
            chipThreeSize = "";
            endJourneyShowQuote(section, noRepairMsg);
        }

        //console.log('chipBeenAlreadyRepaired: ' + chipBeenAlreadyRepaired);
    });

    // section-damage-details-chip-number
    $('.section-damage-details-chip-number').on('change', '.small-squares-radio input', function () {
        var section = $(this).closest('.section-damage-details-chip-number');
        howManyChips = $(this).val();
        var noRepairMsg = $('.notification-messages #noRepairMsg').html();

        //Reset next global values
        chipOneArea = "";
        chipOneSize = "";
        chipTwoArea = "";
        chipTwoSize = "";
        chipThreeArea = "";
        chipThreeSize = "";
        $('.section-damage-details-chip-where [class^="chip-"]:not(".d-none")').addClass('d-none');

        if (howManyChips < 4) {
            hideNextSection(section);
            showNextSection('.section-damage-details-chip-where');
            $('.section-damage-details-chip-where .chip-1').removeClass('d-none');
            if (section.find('.notification-box')[0]) {
                section.find('.notification-box')[0].remove();
            }
        }

        switch (howManyChips) {
            case '1':
                numOfChips = 1;
                break;
            case '2':
                numOfChips = 2;
                break;
            case '3':
                numOfChips = 3;
                break;
            case '4':
                numOfChips = 4;
                hideNextSection(section);
                endJourneyShowQuote(section, noRepairMsg);
                break;
        }
    });

    //Area Picker
    $('.section-damage-details-chip-where .zoneContainer').on('click', '.clickable.zone', function () {
        var area = $(this);
        var areaType = area.data('original-title');
        var section = $(this).closest('[class^="chip-"]');
        var chipNum = section.closest('div[class^="chip-"]:not(.d-none)').attr('class').replace('chip-', '');

        section.find('.clickable.zone.active').removeClass('active');
        area.addClass('active');
        section.find('.sub-section.d-none').removeClass('d-none');
        srollTo(section.find('.sub-section'));

        switch (chipNum) {
            case '1':
                chipOneArea = areaType;
                break;
            case '2':
                chipTwoArea = areaType;
                break;
            case '3':
                chipThreeArea = areaType;
                break;
        }

        if (areaType == "A") {
            section.find('.section-damage-details-chip-sm-or-l').addClass('is-10mm');
        } else {
            section.find('.section-damage-details-chip-sm-or-l').removeClass('is-10mm');
        }
    });

    // section-damage-details-chip-sm-or-l
    $('.section-damage-details-chip-sm-or-l').on('change', '.radio-btns input', function () {
        var section = $(this).closest('.section-damage-details-chip-sm-or-l');
        var chipNum = section.closest('div[class^="chip-"]:not(.d-none)').attr('class').replace('chip-', '');
        var chipIsSmall = $(this).attr('value');
        var noRepairMsg = $('.notification-messages #noRepairChipMsg').html();

        switch (chipNum) {
            case '1':
                chipOneSize = chipIsSmall;
                break;
            case '2':
                chipTwoSize = chipIsSmall;
                break;
            case '3':
                chipThreeSize = chipIsSmall;
                break;
        }

        if (chipIsSmall == 'True') {

            if (section.find('.notification-box')[0]) {
                section.find('.notification-box')[0].remove();
            }

            if (numOfChips == chipNum) {

                $('.disabled-next-section').addClass('d-none');
                if ($('#form-damage-details').hasClass('quote-damage')) {
                    $('.next-section').removeClass('d-none');
                    $('.next-section .quote-btn').removeClass('d-none');
                } else {
                    $('.quote-holder, .repairable').removeClass('d-none');
                }

                srollTo($('.quote-holder'));
                updateQuoteTable()

                if (numOfChips != 1) {
                    $('.added-chip-' + numOfChips).removeClass('d-none');
                }
            }

            if (chipNum < numOfChips) {
                section.closest('div[class^="chip-"]:not(.d-none)').find('.section-add-next-chip').removeClass('d-none');
                srollTo(section.closest('div[class^="chip-"]:not(.d-none)').find('.section-add-next-chip'));
            }
        }
        else {
            endJourneyShowQuote(section, noRepairMsg);
            section.closest('div[class^="chip-"]:not(.d-none)').find('.section-add-next-chip').addClass('d-none');
        }
    });

    $('.section-damage-details-chip-where .section-add-next-chip').on('click', '.btnShowNextChip', function () {
        var btn = $(this);
        var currentChip = parseInt(btn.closest('div[class^="chip-"]:not(.d-none)').attr('class').replace('chip-', ''));
        var NextChip = currentChip + 1;
        $('.chip-' + currentChip).addClass('d-none');
        $('.chip-' + NextChip).removeClass('d-none');
        $('.added-chip-' + currentChip).removeClass('d-none');
        srollTo($('.section-damage-details-chip-number'));
    });

    $('.a-added-chip').on('click', function () {
        var btn = $(this);
        var currentChip = btn.data('chip-target');
        $('.section-damage-details-chip-where [class^="chip-"]:not(.d-none)').addClass('d-none');
        $('.section-damage-details-chip-where .chip-' + currentChip).removeClass('d-none');
        srollTo($('.section-damage-details-chip-number'));
        $('.section-damage-details-chip-where .chip-' + currentChip).find('.section-add-next-chip').addClass('d-none');
    });

    $('.quote-holder table.table-quote').on('click', '.btn-reset-style', function () {
        var btn = $(this);
        var windowTarget = btn.data('window-target');
        var windowTargetId = '#' + windowTarget;
        var carWindowContainer = $('.car-windows');

        carWindowContainer.find(windowTargetId).removeClass('active');
        windowsToChange.splice(windowsToChange.indexOf(windowTarget), 1);

        if (windowTarget == 'windscreen') {
            hideNextSection($('.section-damaged-areas'));
            windSreenIsNotCracked = "";
            chipBeenAlreadyRepaired = "";
            numOfChips = 0;
            chipOneArea = "";
            chipOneSize = "";
            chipTwoArea = "";
            chipTwoSize = "";
            chipThreeArea = "";
            chipThreeSize = "";
        } else {
            if ($('.section-damaged-areas').find('.notification-box')[0]) {
                $('.section-damaged-areas').find('.notification-box')[0].remove();
            }
        }

        if (windowsToChange.length == 0) {
            hideEndjourneyshowquote();
        }

        updateQuoteTable();
    });

    $('.next-section').on('click', '#getQuote', function () {
        var btn = $(this);
        var section = btn.closest('.next-section');
        postDamageDetailsForm(section);
    });

    function showNextSection(nextSection) {
        $(nextSection).removeClass('d-none');
        srollTo(nextSection);
        hideEndjourneyshowquote();
    }
    function hideNextSection(curentSection) {
        curentSection.nextAll('.section-form').addClass('d-none');
        clearFormElements(curentSection.nextAll('.section-form'));
        curentSection.nextAll('.section-form').find('.notification-box').remove();
    }

    function endJourneyShowQuote(section, msg) {
        section.append(msg);
        srollTo(section);
        updateQuoteTable();
        $('.disabled-next-section').addClass('d-none');
        if ($('#form-damage-details').hasClass('quote-damage')) {
            $('.next-section').removeClass('d-none');
            $('.next-section .quote-btn').removeClass('d-none');
            $('.next-section .price-quote:not(.d-none)').addClass('d-none');
            $('.next-section .call-us-section:not(.d-none)').addClass('d-none');

        } else {
            $(".quote-holder").removeClass("d-none");
        }
    }

    function hideEndjourneyshowquote() {
        $('.disabled-next-section').removeClass('d-none');
        if ($('#form-damage-details').hasClass('quote-damage')) {
            $('.next-section').addClass('d-none');
            $('.next-section .quote-btn').addClass('d-none');
            $('.price-quote').addClass('d-none');
            $('.call-us-section').addClass('d-none');
        } else {
            $('.quote-holder').addClass('d-none');
        }
    }

    function clearFormElements(ele) {

        $(ele).find(':input').each(function () {
            switch (this.type) {
                case 'password':
                case 'select-multiple':
                case 'select-one':
                case 'text':
                case 'textarea':
                    $(this).val('');
                    break;
                case 'checkbox':
                case 'radio':
                    this.checked = false;
            }
        });

        $(ele).find('.zoneContainer .clickable.zone.active').removeClass('active');
        $(ele).find('.section-add-next-chip:not(.d-none)').addClass('d-none');
        $('.chip-notification:not(.d-none)').addClass('d-none');

    }

    function srollTo(target) {
        if ($(target).length) {
            $('html,body').animate({ scrollTop: $(target).offset().top - 200 }, 'slow');
        }
    }

    function updateQuoteTable() {
        $('#SelectedDamagedWindowIds').val(windowsToChange.toString());

        if (!!windSreenIsNotCracked) {
            $('#IsWindscreenDamaged').val('True');
        } else {
            $('#IsWindscreenDamaged').val('False');
        }

        if (chipOneArea == "") {
            $('#FirstChipWindscreenDamageLocationId').val('');
        } else {
            $('#FirstChipWindscreenDamageLocationId').val(chipOneArea);
        }

        if (chipTwoArea == "") {
            $('#SecondChipWindscreenDamageLocationId').val('');
        } else {
            $('#SecondChipWindscreenDamageLocationId').val(chipTwoArea);
        }

        if (chipThreeArea == "") {
            $('#ThirdChipWindscreenDamageLocationId').val('');
        } else {
            $('#ThirdChipWindscreenDamageLocationId').val(chipThreeArea);
        }
    }

    function getBookflowDamagesVal() {

        if ($('#SelectedDamagedWindowIds').length == 0) {
            return false;
        }

        var getSelectedDamagedWindowIds = $('#SelectedDamagedWindowIds').val();
        var getIsWindscreenDamaged = $('#IsWindscreenDamaged').val();
        var getWindSreenIsNotCracked = $('.section-damage-details-chip-size .new-big-circles-radio input:checked').val();
        var getChipBeenAlreadyRepaired = $('.section-damage-details-chip-new-old .small-squares-radio input:checked').val();
        var getNumOfChips = $('.section-damage-details-chip-number .small-squares-radio input:checked').val();
        var getChipOneArea = $('#FirstChipWindscreenDamageLocationId').val();
        var getChipOneSize = $('.section-damage-details-chip-where .chip-1 .section-damage-details-chip-sm-or-l input:checked').val();
        var getChipTwoArea = $('#SecondChipWindscreenDamageLocationId').val();
        var getChipTwoSize = $('.section-damage-details-chip-where .chip-2 .section-damage-details-chip-sm-or-l input:checked').val();
        var getChipThreeArea = $('#ThirdChipWindscreenDamageLocationId').val();
        var getChipThreeSize = $('.section-damage-details-chip-where .chip-3 .section-damage-details-chip-sm-or-l input:checked').val();

        if (getSelectedDamagedWindowIds.length) {
            windowsToChange = getSelectedDamagedWindowIds.split(",");
            for (var i = 0; i < windowsToChange.length; i++) {
                $('.car-windows #' + windowsToChange[i]).addClass('active');
            }

            if (windowsToChange.indexOf('windscreen') !== -1) {
                $('.section-damage-details-chip-size').removeClass('d-none');

                if (getWindSreenIsNotCracked == 'True') {
                    windSreenIsNotCracked = getWindSreenIsNotCracked;
                    $('.section-damage-details-chip-new-old').removeClass('d-none');

                    if (getChipBeenAlreadyRepaired == 'False') {
                        chipBeenAlreadyRepaired = getChipBeenAlreadyRepaired;
                        $('.section-damage-details-chip-number').removeClass('d-none');

                        if (!!getNumOfChips) {
                            numOfChips = getNumOfChips;
                            if (getNumOfChips < 4) {
                                $('.section-damage-details-chip-where').removeClass('d-none');
                            }

                            switch (getNumOfChips) {
                                case '1':
                                    chipOneArea = getChipOneArea;
                                    chipOneSize = getChipOneSize;
                                    var chipOneAreaLetter = getChipOneArea.replace("Zone ", "");
                                    $('.section-damage-details-chip-where .chip-1 .zoneContainer #' + chipOneAreaLetter).addClass('active');
                                    $('.section-damage-details-chip-where .chip-1').removeClass('d-none');
                                    $('.section-damage-details-chip-where .chip-1 .section-damage-details-chip-sm-or-l').removeClass('d-none');
                                    break;
                                case '2':
                                    chipOneArea = getChipOneArea;
                                    chipOneSize = getChipOneSize;
                                    chipTwoArea = getChipTwoArea;
                                    chipTwoSize = getChipTwoSize;
                                    var chipTwoAreaLetter = getChipTwoArea.replace("Zone ", "");
                                    $('.section-damage-details-chip-where .chip-2 .zoneContainer #' + chipTwoAreaLetter).addClass('active');
                                    $('.section-damage-details-chip-where .chip-2').removeClass('d-none');
                                    $('.section-damage-details-chip-where .chip-2 .section-damage-details-chip-sm-or-l').removeClass('d-none');
                                    $('.added-chip-1').removeClass('d-none');
                                    $('.added-chip-2').removeClass('d-none');
                                    break;
                                case '3':
                                    chipOneArea = getChipOneArea;
                                    chipOneSize = getChipOneSize;
                                    chipTwoArea = getChipTwoArea;
                                    chipTwoSize = getChipTwoSize;
                                    chipThreeArea = getChipThreeArea;
                                    chipThreeSize = getChipThreeSize;
                                    var chipThreeAreaLetter = getChipThreeArea.replace("Zone ", "");
                                    $('.section-damage-details-chip-where .chip-3 .zoneContainer #' + chipThreeAreaLetter).addClass('active');
                                    $('.section-damage-details-chip-where .chip-3').removeClass('d-none');
                                    $('.section-damage-details-chip-where .chip-3 .section-damage-details-chip-sm-or-l').removeClass('d-none');
                                    $('.added-chip-1').removeClass('d-none');
                                    $('.added-chip-2').removeClass('d-none');
                                    $('.added-chip-3').removeClass('d-none');
                                    break;
                            }

                            $('.disabled-next-section').addClass('d-none');
                            if ($('#form-damage-details').hasClass('quote-damage')) {
                                $('.next-section').removeClass('d-none');
                                $('.next-section .quote-btn').removeClass('d-none');
                            } else {
                                $('.quote-holder').removeClass('d-none');
                            }
                            updateQuoteTable();
                        }

                    } else {
                        chipBeenAlreadyRepaired = getChipBeenAlreadyRepaired;
                        $('.disabled-next-section').addClass('d-none');
                        if ($('#form-damage-details').hasClass('quote-damage')) {
                            $('.next-section').removeClass('d-none');
                            $('.next-section .quote-btn').removeClass('d-none');
                        } else {
                            $('.quote-holder').removeClass('d-none');
                        }
                        updateQuoteTable();
                    }

                } else {
                    windSreenIsNotCracked = getWindSreenIsNotCracked;
                    $('.disabled-next-section').addClass('d-none');
                    if ($('#form-damage-details').hasClass('quote-damage')) {
                        $('.next-section').removeClass('d-none');
                        $('.next-section .quote-btn').removeClass('d-none');
                    } else {
                        $('.quote-holder').removeClass('d-none');
                    }
                    updateQuoteTable();
                }
            } else {
                $('.disabled-next-section').addClass('d-none');
                if ($('#form-damage-details').hasClass('quote-damage')) {
                    $('.next-section').removeClass('d-none');
                    $('.next-section .quote-btn').removeClass('d-none');
                } else {
                    $('.quote-holder').removeClass('d-none');
                }
                updateQuoteTable();
            }
        }
    }

    function postDamageDetailsForm(section) {
        var section = section;
        var postData = $('#form-damage-details').serializeArray();
        //var windowsToChangeLength = windowsToChange.length == 1;
        //var hasWindscreen = windowsToChange.indexOf('windscreen') !== -1;
        //var showQuote = windowsToChangeLength && hasWindscreen;

        section.find('.getQuoteSpinner').removeClass('d-none');

        $.post(
            "/Umbraco/Api/BookflowApi/SetDamageDetailsAndGetPrice",
            postData,
            function (data) {
                section.find('.getQuoteSpinner').addClass('d-none');
                if (data.IsPriceFound) {
                    //Update Quote Table in DOM
                    var tableQuote = $('.next-section .table-quote');
                    var tableQuoteTotalVal = 0;
                    tableQuote.find('tr:not(".sub-total")').remove();
                    var windowName = 'Windscreen';
                    var windowPrice = data.PartPriceDetails.PriceIncVAT;
                    tableQuoteTotalVal = windowPrice;
                    //tableQuote.prepend('<tr><td>' + windowName + '</td><td>£' + windowPrice + '</td><td><button class="btn-reset-style" type="button">Remove</button></td></tr>');
                    tableQuote.find('.total-val').text(tableQuoteTotalVal);
                    $('.price-quote').removeClass('d-none');
                    $('.call-us-section').addClass('d-none');
                    $('.next-section .quote-btn').addClass('d-none');
                } else {
                    $('.price-quote').addClass('d-none');
                    $('.call-us-section').removeClass('d-none');
                    $('.next-section .quote-btn').addClass('d-none');
                }
            }
        );
    }
});

