var storeMap;
function initMap() {
    var storeLocationMaps = $('.store-location');
    storeLocationMaps.each(function () {
        var storeLocationLat = $(this).data('location-lat');
        var storeLocationLng = $(this).data('location-lng');
        storeMap = new google.maps.Map($(this)[0], {
            center: { lat: storeLocationLat, lng: storeLocationLng },
            zoom: 14
        });
        addMarker(storeLocationLat, storeLocationLng , storeMap);
    })

    // Adds a marker to the map.
    function addMarker(storeLocationLat, storeLocationLng, map) {
        // Add the marker at the clicked location, and add the next-available label
        // from the array of alphabetical characters.
        var marker = new google.maps.Marker({
            position: { lat: storeLocationLat, lng: storeLocationLng },
            map: map
        });
    }
}
$(document).ready(function () {

    $('.accordion-carousel #accordion .card').eq(0).find('.collapse').collapse('show');

    $('.accordion-carousel .collapse').on('shown.bs.collapse', function (e) {
        google.maps.event.trigger(storeMap, "resize");
        initMap();
    });

    $('.owl-carousel.appointment-carousel').each(function () {
        $(this).owlCarousel({
            mouseDrag: false,
            touchDrag: false,
            margin: 10,
			loop: false,
			navigation: false,
			paginationSpeed: 400,
			items: 1,
            nav: true,
            autoHeight: true,
            navText: ['<i class="awicon_chevron-left"></i>Previous week', 'Next week<i class="awicon_chevron-right"></i>']
		});
    });

    $('.owl-carousel.hub-carousel').each(function () {
        $(this).owlCarousel({
            loop: true,
            navigation: false,
            animateOut: 'fadeOut',
            paginationSpeed: 400,
            items: 1,
            nav: true,
            navText: ["<", ">"]
        });
    });

	$('.accordion-carousel .card-header a').on('click', function () {
		if ($(this).hasClass('collapsed')) {
			$('.card').removeClass('active');
			$(this).closest('.card').addClass('active');
		}
		else {
			$('.card').removeClass('active');
		}
	});

    $('.getAppointmentDetails').on('click', function () {
        var btn = $(this);
        var isCustomAdress = $('.data-flags #isCustomAdress').data('iscustomadress').toString();
        var isHubJob = $('.data-flags #isHubJob').data('ishubjob').toString();

        var postData = {};

        $('.section-appointment-calendar').addClass('d-none');
        $('.section-end-journey').addClass('d-none');
        $('.disabled-next-section').removeClass('d-none');
        $('.loading-appointment-calendar.d-none').removeClass('d-none');
        $('#container-no-availibility-found:not(.d-none)').addClass('d-none');

        if (isCustomAdress == 'true' && isHubJob == 'false') {
            postData = {
                BookingId: $('#BookingId').val(),
                AddressId: '',
                AddressLineOne: $('.section-address-form:not(.d-none) .address-input-line-one').val(),
                AddressLineTwo: $('.section-address-form:not(.d-none) .address-input-line-two').val(),
                AddressPostcode: $('.section-address-form:not(.d-none) .postcode-input').val(),
                AddressTown: $('.section-address-form:not(.d-none) .address-input-town').val(),
                WeekToShow: 1
            };

        } else if (isCustomAdress == 'false' && isHubJob == 'false') {
            postData = {
                BookingId: $('#BookingId').val(),
                AddressId: $('#current-booking-address-id').val(),
                AddressLineOne: $('#current-booking-address-line-one').val(),
                AddressLineTwo: $('#current-booking-address-line-two').val(),
                AddressPostcode: $('#current-booking-address-postcode').val(),
                AddressTown: $('#current-booking-address-town').val(),
                WeekToShow: 1
            };

        } else if (isHubJob == 'true') {
            var locationValue = btn.data('value');
            var locationCardContainer = btn.closest('.card');
            var locationAccordionContainer = btn.closest('.accordion-carousel');
            var invisibleInputsContainer = locationAccordionContainer.find('.invisible-inputs');

            postData = {
                BookingId: $('#BookingId').val(),
                AddressId: btn.closest('.card').data('metrix-address-id'),
                AddressLineOne: btn.closest('.card').data('metrix-address-line1'),
                AddressLineTwo: btn.closest('.card').data('metrix-address-line2'),
                AddressPostcode: btn.closest('.card').data('metrix-address-postcode'),
                AddressTown: btn.closest('.card').data('metrix-address-town'),
                WeekToShow: 1
            };

            locationAccordionContainer.find('.card').removeClass('checked');
            locationAccordionContainer.find('#' + locationValue).addClass('checked');
            locationCardContainer.find('.collapse.show').collapse('hide');
            invisibleInputsContainer.find('input:radio[value=' + locationValue + ']').prop('checked', true);

        } else {
            return false;
        }

        $.post(
            "/Umbraco/Api/BookflowApi/SetJobLocationAndGetAppointmentDetails",
            postData,
            function (data) {

                if (data) {
                    //Reset dom table
                    $('.appointment-table tr:not(.table-row-days)').remove();
                    $('.appointment-table:not(.mobile) tr.table-row-days th').remove();
                    $('.appointment-table:not(.mobile) tr.table-row-days').html('<th></th>');
                    $('.section-appointment-calendar .notification-box .priority-notification').addClass('d-none');

                    $('.loading-appointment-calendar:not(.d-none)').addClass('d-none');
                    $('.section-appointment-calendar.d-none').removeClass('d-none');

                    var existingTimeOfSlot = [];
                    var addressToPrint = postData.AddressLineOne + ' ' + postData.AddressPostcode + ' ' + postData.AddressTown;

                    $('.section-appointment-calendar .appointment-location').text(addressToPrint);

                    //we only show 2 weeks
                    for (i = 0; i < 14; i++) {
                        var displayDate = data.AppointmentAvailabilty[i].DisplayDate;
                        var displayDay = data.AppointmentAvailabilty[i].DisplayDay;
                        var weekNum = "";

                        if (i <= 6) {
                            weekNum = '.week-one';
                        } else {
                            weekNum = '.week-two';
                        }

                        if (i == 7) {
                            var existingTimeOfSlot = [];
                        }

                        $(weekNum + ' .appointment-table:not(.mobile) tr.table-row-days').append('<th data="' + displayDate + '">' + displayDay + ' ' + displayDate + '</th>');
                        $(weekNum + ' .appointment-table.mobile').append('<tr rowspan=2 data-day="' + displayDate + '">' + '<th>' + displayDay + ' ' + displayDate + '</th><td><table class="sub-row"></table></td></tr>');

                        for (j = 0; j < data.AppointmentAvailabilty[i].AppointmentSlots.length; j++) {
                            var timeOfSlot = data.AppointmentAvailabilty[i].AppointmentSlots[j].TimeOfSlot;
                            var isTimeOfSlotAvailable = data.AppointmentAvailabilty[i].AppointmentSlots[j].IsAvailable;
                            var isTimeOfSlotPriority = data.AppointmentAvailabilty[i].AppointmentSlots[j].IsPriority;

                            var metrixData = {
                                appointmentId: data.AppointmentAvailabilty[i].AppointmentSlots[j].MetrixAppointmentId,
                                appointmentRequestId: data.AppointmentAvailabilty[i].AppointmentSlots[j].MetrixAppointmentRequestId,
                                available: data.AppointmentAvailabilty[i].AppointmentSlots[j].MetrixAvailable,
                                offerStart: data.AppointmentAvailabilty[i].AppointmentSlots[j].MetrixOfferStart,
                                offerEnd: data.AppointmentAvailabilty[i].AppointmentSlots[j].MetrixOfferEnd,
                                offerValue: data.AppointmentAvailabilty[i].AppointmentSlots[j].MetrixOfferValue,
                                planId: data.AppointmentAvailabilty[i].AppointmentSlots[j].MetrixPlanId,
                                reasonTypeId: data.AppointmentAvailabilty[i].AppointmentSlots[j].MetrixReasonTypeId,
                            };

                            var btnDataAttr = "";
                            Object.keys(metrixData).forEach(function (key) {
                                btnDataAttr += 'data-metrix-' + key + '="';
                                btnDataAttr += metrixData[key] + '" ';
                            });

                            if (existingTimeOfSlot.indexOf(timeOfSlot) < 0) {
                                existingTimeOfSlot.push(timeOfSlot);
                                $(weekNum + ' .appointment-table:not(.mobile)').append('<tr data-slot="' + timeOfSlot + '"><th>' + timeOfSlot + '</th></tr>');
                            }

                            if (isTimeOfSlotAvailable && isTimeOfSlotPriority) {
                                $(weekNum + ' .appointment-table:not(.mobile) tr[data-slot="' + timeOfSlot + '"]').append('<td><button type="button" class="priority"' + btnDataAttr + '><span class="has-icon"></span></button></td>');
                                $(weekNum + ' .appointment-table.mobile tr[data-day="' + displayDate + '"] table.sub-row').append('<tr><td><button type="button" class="priority"' + btnDataAttr + '>' + timeOfSlot + '<span class="has-icon"></span></button></td></tr>');
                                $('.section-appointment-calendar .notification-box .priority-notification.d-none').removeClass('d-none');
                            } else if (isTimeOfSlotAvailable && !isTimeOfSlotPriority) {
                                $(weekNum + ' .appointment-table:not(.mobile) tr[data-slot="' + timeOfSlot + '"]').append('<td><button type="button" ' + btnDataAttr + '></button></td>');
                                $(weekNum + ' .appointment-table.mobile tr[data-day="' + displayDate + '"] table.sub-row').append('<tr><td><button type="button"' + btnDataAttr + '>' + timeOfSlot + '</button></td></tr>');

                            } else {
                                $(weekNum + ' .appointment-table:not(.mobile) tr[data-slot="' + timeOfSlot + '"]').append('<td></td>');
                                $(weekNum + ' .appointment-table.mobile tr[data-day="' + displayDate + '"] table.sub-row').append('<tr><td><span class="style-btn">' + timeOfSlot + '</span></td></tr>');
                            }
                        }
                    }

                    $('.owl-carousel.appointment-carousel').trigger('refresh.owl.carousel');

                } else {
                    $('#container-no-availibility-found').removeClass('d-none');
                    $('.loading-appointment-calendar:not(.d-none)').addClass('d-none');
                }
            }
        );
    });

    $('.section-appointment-calendar').on('click', '.appointment-table button', function () {
        var btn = $(this);
        var getColIndex = btn.closest('td').index();
        var getRowIndex = btn.closest('tr').index();
        var tableParent = btn.closest('.appointment-table');

        var getMetrixAppointmentId = btn.data('metrix-appointmentid'),
            getMetrixAvailable = btn.data('metrix-available'),
            getMetrixOfferValue = btn.data('metrix-offervalue'),
            getMetrixAppointmentRequestId = btn.data('metrix-appointmentrequestid'),
            getMetrixOfferEnd = btn.data('metrix-offerend'),
            getMetrixOfferStart = btn.data('metrix-offerstart'),
            getMetrixPlanId = btn.data('metrix-planid'),
            getMetrixReasonTypeId = btn.data('metrix-reasontypeid');

        $('.appointment-table button').removeClass('active');
        btn.addClass('active');

        if (!tableParent.hasClass('mobile')) {
            $('.appointment-table:not(.mobile) th.active').removeClass('active');
            tableParent.find('.table-row-days th').eq(getColIndex).addClass('active');
            tableParent.find('tr').eq(getRowIndex).find('th').addClass('active');
        }

        $('#MetrixAppointmentId').val(getMetrixAppointmentId);
        $('#MetrixAvailable').val(getMetrixAvailable);
        $('#MetrixOfferValue').val(getMetrixOfferValue);
        $('#MetrixAppointmentRequestId').val(getMetrixAppointmentRequestId);
        $('#MetrixOfferEnd').val(getMetrixOfferEnd);
        $('#MetrixOfferStart').val(getMetrixOfferStart);
        $('#MetrixPlanId').val(getMetrixPlanId);
        $('#MetrixReasonTypeId').val(getMetrixReasonTypeId);

        $('.section-end-journey').removeClass('d-none');
        $('.disabled-next-section').addClass('d-none');
    });

    $('.showClientJob').on('click', function () {
        var btn = $(this);
        $('.section-store-job').addClass('d-none');
        $('.section-client-home-location').removeClass('d-none');
        $('.over-footer').toggleClass('d-none');
        $('.data-flags #isHubJob').data('ishubjob', 'false');
        $('.section-appointment-calendar').addClass('d-none');
        $('.section-end-journey').addClass('d-none');
        $('.disabled-next-section').removeClass('d-none');
        $('#accordion .card').removeClass('checked');
        $('#container-no-availibility-found:not(.d-none)').addClass('d-none');
    });

    $('.showStoreJob').on('click', function () {
        var btn = $(this);
        $('.section-client-home-location').addClass('d-none');
        $('.section-store-job').removeClass('d-none');
        $('.over-footer').toggleClass('d-none');
        $('.data-flags #isHubJob').data('ishubjob', 'true');
        $('.section-appointment-calendar').addClass('d-none');
        $('.section-end-journey').addClass('d-none');
        $('.disabled-next-section').removeClass('d-none');
        $('.accordion-carousel #accordion .card').eq(0).find('.collapse').collapse('show');
        $('#container-no-availibility-found:not(.d-none)').addClass('d-none');
    });

    $('.clientJobAddressPicker').on('change', function () {
        var clientJobAddressPickerValue = $(this).find('input:checked').val();

        if (clientJobAddressPickerValue == "clientCustomAddress") {
            $('.section-address-form').removeClass('d-none');
            $('.section-end-journey-client-job-form:not(.d-none)').addClass('d-none');
            $('.data-flags #isCustomAdress').data('iscustomadress', 'true');
            $('.postcode-lookup #container-full-address').hide();
            $('.postcode-lookup #container-address-fields').hide();
        } else {
            $('.section-address-form').addClass('d-none');
            $('.section-end-journey-client-job-form').removeClass('d-none');
            $('.data-flags #isCustomAdress').data('iscustomadress', 'false');
            $('.section-appointment-calendar').addClass('d-none');
            $('.section-end-journey').addClass('d-none');
            $('.disabled-next-section').removeClass('d-none');
        }
    });

    $(".section-client-home-location #insurance-form-one-full-address").change(function () {
        $('.section-end-journey-client-job-form').removeClass('d-none');
    });
});