$(document).ready(function () {
    if (!$('#PolicyHoldersDateOfBirth').length) {
        return false;
    }

    $('.date-picker').each(function () {
        var getInputId = $(this).find('.date-picker--date').attr('id');
        updateDateInputs('#' + getInputId);
    });

    function updateDateInputs(inputId) {
        var getDateVal = $(inputId).attr('value');

        if (!!$(getDateVal)) {
            var getDateValArray = getDateVal.split('-');
            var getDateValYear = getDateValArray[0];
            var getDateValMonth = getDateValArray[1];
            var getDateValDay = getDateValArray[2];
            var dateContainer = $(inputId).closest('.date-picker');
            var datePickerInputDay = dateContainer.find('.date-picker--day');
            var datePickerInputMonth = dateContainer.find('.date-picker--month');
            var datePickerInputYear = dateContainer.find('.date-picker--year');

            datePickerInputDay.val(getDateValDay);
            datePickerInputMonth.val(getDateValMonth);
            datePickerInputYear.val(getDateValYear);
        }
    }
});