$(document).ready(function () {

    $('#callbackbookflow #insurance-form-one-mobile-number, #callbackbookflow #insurance-form-one-email ').change(function () {
        var emailInput = $('#callbackbookflow #insurance-form-one-email').val();
        var phoneNumberInput = $('#callbackbookflow #insurance-form-one-mobile-number').val();
        if (emailInput || phoneNumberInput) {
            $('#callbackbookflow #insurance-form-one-mobile-number').removeClass('input-validation-error');
            $('#callbackbookflow #insurance-form-one-email').removeClass('input-validation-error');
            $(".one-contact-method").addClass("d-none");
        } else {
            $('#callbackbookflow #insurance-form-one-mobile-number').addClass('input-validation-error');
            $('#callbackbookflow #insurance-form-one-email').addClass('input-validation-error');
            $(".one-contact-method").removeClass("d-none");
        }
    });

    $('#callbackbookflow button[type="submit"].btn-primary').on('click', function (e) {
        e.preventDefault();

        var emailInput = $('#callbackbookflow #insurance-form-one-email').val();
        var phoneNumberInput = $('#callbackbookflow #insurance-form-one-mobile-number').val();
        var regexPhoneNumber = /^(\+)?(\d)*$/;
        var regexEmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        var testPhoneNumber = regexPhoneNumber.test(phoneNumberInput);
        var testEmail = regexEmail.test(emailInput);
        var isEmailValid = !!emailInput && testEmail;
        var isPhoneValid = !!phoneNumberInput && testPhoneNumber;

        //console.log('isPhoneValid: ' + isPhoneValid);
        //console.log('testEmail: ' + testEmail);

        if (isPhoneValid || isEmailValid) {
            $('#callbackinput').val('true');
            $('#callbackbookflow').trigger('submit');
        } else if (!!emailInput && !!phoneNumberInput) {
            $('#callbackbookflow #insurance-form-one-mobile-number').addClass('input-validation-error');
            $('#callbackbookflow #insurance-form-one-email').addClass('input-validation-error');
            $(".one-contact-method").removeClass("d-none");
        } else {
            $(".one-contact-method").removeClass("d-none");
            if (!isPhoneValid) {
                $('#callbackbookflow #insurance-form-one-mobile-number').addClass('input-validation-error');
            }

            if (!testEmail) {
                $('#callbackbookflow #insurance-form-one-email').addClass('input-validation-error');
            }
        }
    });
});