$(document).ready(function() {
    $('#submit-fleet-account-number-lookup').click(function() {
        $(this).siblings(".spinner-wrapper").toggleClass("spinner-active");
        //$("#container-no-registration-number-found").hide();

        var postData = {
            AccountNumber: $('#fleet-account-number').val()
        };

        $.post(
            "/Umbraco/Api/BookflowApi/FleetAccountNumberLookup",
            postData,
            function(data) {

                //$("#container-vehicle-details").show();

                if (data) {
                    //  TODO: populate the Ui when we know what data will be returned
                } else {
                    //$("#container-no-registration-number-found").show();
                }
                $('#submit-fleet-account-number-lookup').siblings(".spinner-wrapper").toggleClass("spinner-active");
            }
        );
    });

    var isVehicleLeased = $('.isLeasedVehicle input:checked').val();

    if (isVehicleLeased == "True") {
        $('.section-vehicle-is-leased').removeClass('d-none');
    }

    $('.isLeasedVehicle').on('change', function () {
        isVehicleLeased = $('.isLeasedVehicle input:checked').val();
        if (isVehicleLeased == "False") {
            $('.section-vehicle-is-leased').addClass('d-none');
        } else {
            $('.section-vehicle-is-leased').removeClass('d-none');
        }
    });
});