$(document).ready(function () {

    function geoSuccess(position) {
        printAddress(position.coords.latitude, position.coords.longitude);
    }

    function geoError(err) {
        $('.mapSearchText').focus();
    }

    function printAddress(latitude, longitude) {
        var geocoder = new google.maps.Geocoder();
        var yourLocation = new google.maps.LatLng(latitude, longitude);

        geocoder.geocode({ 'latLng': yourLocation }, function (results, status) {
            if (status == google.maps.GeocoderStatus.OK) {
                var theFormatedPostcode = (JSON.stringify(results[0].address_components[results[0].address_components.length - 1].long_name).replace(/['"]+/g, ''));
                if (results[0]) {
                    globalGetPostcodeLookupContainer.find('.postcode-input').val(theFormatedPostcode);
                    submitPostcode(globalGetPostcodeLookupContainer);
                } else {
                    error('No results');
                }
            }
        });
    }

    function submitPostcode(btn) {
        var getPostcodeLookupContainer = btn.closest('.postcode-lookup');
        btn.siblings(".spinner-wrapper").addClass("spinner-active");
        var postData = {
            postcode: getPostcodeLookupContainer.find('.postcode-input').val()
        };

        $.post(
            "/Umbraco/Api/BookflowApi/PostcodeLookup",
            postData,
            function (data) {
                if (!!data){
                    getPostcodeLookupContainer.find(".address-options").html(postcodeLookupTemplate(data));
                    getPostcodeLookupContainer.find(".address-holder").show();
                    getPostcodeLookupContainer.find('.address-options').closest('.form-group').removeClass('d-none');
                    $("body, html").animate({ scrollTop: getPostcodeLookupContainer.find(".address-holder").offset().top }, 500);
                    getPostcodeLookupContainer.find('.submitPostcode').siblings(".spinner-wrapper").removeClass("spinner-active");
                    getPostcodeLookupContainer.find('.postcode-input').removeClass("input-validation-error");
                    getPostcodeLookupContainer.find('.postcode-holder .field-validation-error').addClass("field-validation-valid").removeClass('field-validation-error').text('');
                } else {
                    getPostcodeLookupContainer.find('.submitPostcode').siblings(".spinner-wrapper").removeClass("spinner-active");
                    getPostcodeLookupContainer.find('.postcode-input').addClass("input-validation-error");
                    getPostcodeLookupContainer.find('.postcode-holder .field-validation-valid').addClass("field-validation-error").removeClass('field-validation-valid').text('Address not found');
                }
                
            }
        );
    }

    function getFormattedPostcode(results, index) {
        return (JSON.stringify(results[index].address_components[results[index].address_components.length - 1].long_name).replace(/['"]+/g, ''))
    }

    if ($("#postcode-lookup-template").length) {
        var postcodeLookupTemplate = Handlebars.compile($("#postcode-lookup-template").html());

        var globalGetPostcodeLookupContainer;
        $('.postcode-lookup .checkCurrentLocation').bind('click',
            function(event) {
                event.preventDefault();
                var getPostcodeLookupContainer = $(this).closest('.postcode-lookup');
                globalGetPostcodeLookupContainer = getPostcodeLookupContainer;

                $(this).siblings(".spinner-wrapper").addClass("spinner-active");
                if (navigator && navigator.geolocation) {
                    navigator.geolocation.getCurrentPosition(geoSuccess, geoError);
                } else {
                }
            });

        $('.submitPostcode').click(function() {
            var btn = $(this);
            var getPostcodeLookupContainer = btn.closest('.postcode-lookup');
            var postcodeVal = getPostcodeLookupContainer.find('.postcode-input').val();
            if (!!postcodeVal) {
                submitPostcode(btn);
            } else {
                getPostcodeLookupContainer.find('.submitPostcode').siblings(".spinner-wrapper").removeClass("spinner-active");
                getPostcodeLookupContainer.find('.postcode-input').addClass("input-validation-error");
                getPostcodeLookupContainer.find('.postcode-holder .field-validation-valid').addClass("field-validation-error").removeClass('field-validation-valid').text('Please enter a postcode');
            }
        });

        $(".address-options").change(function() {
            var getPostcodeLookupContainer = $(this).closest('.postcode-lookup');
            var postcodeIdValue = getPostcodeLookupContainer.find(".address-options").val();

            if (postcodeIdValue === "") {
                getPostcodeLookupContainer.find(".address-inputs-container").hide();
                getPostcodeLookupContainer.find(".hidden-address-id").val("");
                getPostcodeLookupContainer.find(".address-input-line-one").val("");
                getPostcodeLookupContainer.find(".address-input-line-two").val("");
                getPostcodeLookupContainer.find(".address-options").val("");
            } else {
                $(this).parent().siblings(".spinner-wrapper").toggleClass("spinner-active");
                var postData = {
                    postcodeId: postcodeIdValue
                };

                $.post(
                    "/Umbraco/Api/BookflowApi/PostcodeLookupById",
                    postData,
                    function(data) {
                        getPostcodeLookupContainer.find(".hidden-address-id").val(postcodeIdValue);
                        getPostcodeLookupContainer.find(".address-input-line-one").val(data.Postcode.AddressLine1);
                        getPostcodeLookupContainer.find(".address-input-line-two").val(data.Postcode.AddressLine2);
                        getPostcodeLookupContainer.find(".address-input-town").val(data.Postcode.Town);

                        getPostcodeLookupContainer.find(".address-inputs-container").show();
                        getPostcodeLookupContainer.find(".address-options").parent().siblings(".spinner-wrapper")
                            .toggleClass("spinner-active");
                    }
                );
            }

        });

        $('.address-input-line-one').each(function() {
            var getPostcodeLookupContainer = $(this).closest('.postcode-lookup');
            var getAddressLineOneVal = $(this).val();
            if (!!getAddressLineOneVal) {
                getPostcodeLookupContainer.find(".address-options").closest('.form-group').addClass('d-none');
            }
        });
    }
});