var makeFieldId = "#insurance-form-one-make";
var modelFieldId = "#insurance-form-one-model";
var bodyTypeFieldId = "#insurance-form-one-body-type";
var theYearFieldId = "#insurance-form-one-year";
var theHiddenModelId = "#hidden-vehicle-model-id";

$(document).ready(function () {

    $('.ctaToggleContactNumber').on('click', function (e) {
        e.preventDefault();
        var cta = $(this);
        var target = cta.data('target');
        var container = cta.closest('.toggleContactNumber');

        switch (target) {
            case 'landline':
                container.find('.houseNumber').removeClass('d-none');
                container.find('.mobileNumber').addClass('d-none');
                cta.find('span').text(target);
                cta.data('target', 'mobile');
                break;
            case 'mobile':
                container.find('.mobileNumber').removeClass('d-none');
                container.find('.houseNumber').addClass('d-none');
                cta.find('span').text(target);
                cta.data('target', 'landline');
                break;
        }
    });

    $('#submit-registration-lookup').click(function () {
        $(this).siblings(".spinner-wrapper").toggleClass("spinner-active");
        $("#container-no-registration-number-found").hide();

        var postData = {
            VehicleRegistrationNumber: $('#insurance-form-one-registration-number').val()
        };

        $.post(
            "/Umbraco/Api/BookflowApi/VehicleRegistrationLookup",
            postData,
            function (data) {

                $("#container-vehicle-details").show();
                $('.disabled-next-section').addClass('d-none');

                if (data) {
                    $(makeFieldId).val(data.Vehicle.Make);
                    $(modelFieldId).empty().append($('<option>', { value: data.Vehicle.Model, text: data.Vehicle.Model }));
                    $(bodyTypeFieldId).empty().append($('<option>', { value: data.Vehicle.BodyType, text: data.Vehicle.BodyType }));
                    $(theYearFieldId).empty().append($('<option>', { value: data.Vehicle.YearOfManufacture, text: data.Vehicle.YearOfManufacture }));
                    $(theHiddenModelId).val(data.Vehicle.ModelId);
                    $("#IsVRNManualLookup").val("false");
                } else {
                    $("#container-no-registration-number-found").show();
                    $("#IsVRNManualLookup").val("true");
                }
                $('#submit-registration-lookup').siblings(".spinner-wrapper").toggleClass("spinner-active");
            }
        );
    });

    // Vehicle dropdowns section ----------------------------

    lookupMake();

    $(makeFieldId).change(function () {
        $("#IsVRNManualLookup").val("true");
        lookupModel();
    });

    $(modelFieldId).change(function () {
        lookupBodyStyle();
    });

    $(bodyTypeFieldId).change(function () {
        lookupYOM();
    });

    $(theYearFieldId).change(function () {
        $(theHiddenModelId).val($(theYearFieldId + " :selected").data("modelid"));
    });

    function lookupMake() {
        $(makeFieldId).closest('.form-group').find('.spinner').addClass('spinner-active');
        $.ajax({
            dataType: "json",
            url: "/Umbraco/Api/BookflowApi/LookupMakes",
            async: false,
            cache: true,
            success: function (json) {
                $(makeFieldId)
                    .empty()
                    .append('<option selected disabled>Please select</option>');
                for (var i = 0; i < json.length; i++) {
                    $(makeFieldId)
                        .append($('<option>', {
                            value: json[i].Value,
                            text: json[i].Description
                        }));
                }
                $(makeFieldId).closest('.form-group').find('.spinner').removeClass('spinner-active');
            }
        });
    };

    function lookupModel() {
        $(modelFieldId).empty().append('<option selected disabled>Please select</option>');
        $(bodyTypeFieldId).empty().append('<option selected disabled>Please select</option>');
        $(theYearFieldId).empty().append('<option selected disabled>Please select</option>');
        $(theHiddenModelId).val("");
        $(modelFieldId).closest('.form-group').find('.spinner').addClass('spinner-active');
        $.ajax({
            dataType: "json",
            url: "/Umbraco/Api/BookflowApi/LookupModels",
            cache: true,
            data: { 'make': $(makeFieldId + ' :selected').val() }, // encodes ampersands
            success: function (json) {
                for (var i = 0; i < json.length; i++) {
                    $(modelFieldId)
                        .append($('<option>', {
                            value: json[i].Value,
                            text: json[i].Description
                        }));
                }
                $(modelFieldId).closest('.form-group').find('.spinner').removeClass('spinner-active');
            }
        });
    };

    function lookupBodyStyle() {
        $(bodyTypeFieldId).empty().append('<option selected disabled>Please select</option>');
        $(theYearFieldId).empty().append('<option selected disabled>Please select</option>');
        $(theHiddenModelId).val("");
        $(bodyTypeFieldId).closest('.form-group').find('.spinner').addClass('spinner-active');
        $.ajax({
            dataType: "json",
            url: "/Umbraco/Api/BookflowApi/LookupBodyTypes",
            cache: true,
            data: { 'make': $(makeFieldId + ' :selected').val(), 'model': $(modelFieldId + ' :selected').val() },
            success: function (json) {
                for (var i = 0; i < json.length; i++) {
                    $(bodyTypeFieldId)
                        .append($('<option>', {
                            value: json[i].Value,
                            text: json[i].Description
                        }));
                }
                $(bodyTypeFieldId).closest('.form-group').find('.spinner').removeClass('spinner-active');
            }
        });
    };

    function lookupYOM() {
        $(theYearFieldId).empty().append('<option selected disabled>Please select</option>');
        $(theHiddenModelId).val("");
        $(theYearFieldId).closest('.form-group').find('.spinner').addClass('spinner-active');
        $.ajax({
            dataType: "json",
            url: "/Umbraco/Api/BookflowApi/LookupYearOfManufacturer",
            cache: true,
            data: { 'make': $(makeFieldId + ' :selected').val(), 'model': $(modelFieldId + ' :selected').val(), 'bodytype': $(bodyTypeFieldId + ' :selected').val() },
            success: function (json) {
                var currentYear = new Date().getFullYear();
                var earliestYear = json[0].Value;
                var modelID = json[0].ModelId;
                for (var i = currentYear; i >= earliestYear; i--) {
                    $(theYearFieldId)
                        .append($('<option data-modelID="' + modelID + '"></option>')
                            .attr('value', i)
                            .text(i));
                }
                $(theYearFieldId).closest('.form-group').find('.spinner').removeClass('spinner-active');
            }
        });
    };
});
