$(document).ready(function () {
    $('.btn-submit-payment').on('click', function () {
        if ($('#payementAdressPicker input:checked').val() == 'clientCustomAddress'){
            var billingAddressId = $('#AddressId').val();
            var billingAddressLineOne = $('#insurance-form-one-address-line-one').val();
            var billingAddressLineTwo = $('#insurance-form-one-address-line-two').val();
            var billingAddressTown = $('#insurance-form-one-town').val();
            var billingAddressPostcode = $('#insurance-form-one-postcode').val();

            $('#BookingId').val(billingAddressId);
            $('#BillingAddressLineOne').val(billingAddressLineOne);
            $('#BillingAddressLineTwo').val(billingAddressLineTwo);
            $('#BillingAddressTown').val(billingAddressTown);
            $('#BillingAddressPostcode').val(billingAddressPostcode);
        }
            $(this).closest('form').submit();
    });
});